.section {
	background-image: url("https://images.unsplash.com/photo-1581887137877-31283b0086a3?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1348&q=80");
	background-color: #fdf0e0; /* Used if the image is unavailable */
	height: 100vh; /* You must set a specified height */
	/* min-height: 600px; */
	background-position: center; /* Center the image */
	background-repeat: no-repeat; /* Do not repeat the image */
	background-size: contain; /* Resize the background image to cover the entire container */
}

/* .navbar a {
	color: black;
} */

.view {
	background-image: url("https://images.unsplash.com/photo-1504892609977-4019e2d86341?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1351&q=80")
		no-repeat center center;
	background-size: cover;
}

.navbar {
	background-color: transparent;
}

.top-nav-collapse {
	background-color: #4285f4;
}

@media only screen and (max-width: 768px) {
	.navbar {
		background-color: #4285f4;
	}
}

html,
body,
header,
.view {
	height: 100%;
}
